// site setting - defien fonts , color etc

// Typography
$base-font-size: 16px;
$font-1: "Helvectica Neue", Helvetica, Arial, sans-serif;
$font-2: Georgia, sans-serif;
$baseline: $base-font-size * 1.5;


// color
$color-link: #4183C4;
$color-link-hover: #333;

$color-text: #333;
$color-light-text: #777;

$color-hr: #EEE;

$color-1: #4183C4;
$color-2: #EFEFEF;

$color-validation-error: red ;
$color-validation-success: green;

// Positioning
$margin-multiplier: 2;

$general-margin: $baseline * $margin-multiplier;

